import React, { useState, useEffect } from "react";
import Paging from "./pagination";
import { useSelector } from "react-redux";

function Whiskey({ searchTerm }) {
  const whiskey = useSelector((state) => state.whiskey);
  const [currentPage, setCurrentPage] = useState(1);
  const itemsPerPage = 3;
  const indexOfLastItem = currentPage * itemsPerPage;
  const indexOfFirstItem = indexOfLastItem - itemsPerPage;

  const currentItems = searchTerm
    ? whiskey.filter((whiskeys) =>
        whiskeys.name.toLowerCase().includes(searchTerm.toLowerCase())
      )
    : whiskey;

  const totalItemsCount = currentItems.length;

  const handlePageChange = (pageNumber) => {
    setCurrentPage(pageNumber);
  };
  useEffect(() => {
    setCurrentPage(1);
  }, [searchTerm]);

  return (
    <>
      <div>
        {currentItems.length === 0 ? (
          <h2 className="searchFailed"> 일치하는 항목이 없습니다.</h2>
        ) : (
          currentItems
            .slice(indexOfFirstItem, indexOfLastItem)
            .map((whiskeys) => (
              <div key={whiskeys.id}>
                <div className="pageList" style={{ background: "#e3c7a6" }}>
                  <div className="pageImgBox">
                    <img
                      src={`/image/whiskey/${whiskeys.id}.jpg`}
                      alt={whiskeys.name}
                    ></img>
                  </div>
                  <div className="pageInfo">
                    <h3 className="pageTitle">이름 : {whiskeys.name}</h3>
                    <p>타입 : {whiskeys.type}</p>
                    {whiskeys.quality && <p>특징 : {whiskeys.quality}</p>}
                    {whiskeys.price && <p>가격 : {whiskeys.price}기준</p>}
                    <p>도수 : {whiskeys.vol}도</p>
                    <a href={whiskeys.info}>추가정보</a>
                  </div>
                </div>
              </div>
            ))
        )}
        <Paging
          totalItems={totalItemsCount}
          itemsPerPage={itemsPerPage}
          onPageChange={handlePageChange}
        />
      </div>
    </>
  );
}
export default Whiskey;
