import React, { useState, useEffect } from "react";
import { useSelector } from "react-redux";
import Paging from "./pagination";

function CockTail({ searchTerm }) {
  const cockTail = useSelector((state) => state.cockTail);
  const [currentPage, setCurrentPage] = useState(1);
  const itemsPerPage = 3;
  const indexOfLastItem = currentPage * itemsPerPage;
  const indexOfFirstItem = indexOfLastItem - itemsPerPage;

  // 검색어가 존재할 경우 해당 검색어를 포함하는 칵테일만 필터링하여 currentItems에 저장
  const currentItems = searchTerm
    ? cockTail.filter((cock) =>
        cock.name.toLowerCase().includes(searchTerm.toLowerCase())
      )
    : cockTail;

  const totalItemsCount = currentItems.length;

  // 페이지 변경 시마다 페이지 변경 핸들러 호출
  const handlePageChange = (pageNumber) => {
    setCurrentPage(pageNumber);
  };

  // 검색어가 변경될 때마다 페이지 초기화
  useEffect(() => {
    setCurrentPage(1);
  }, [searchTerm]);

  return (
    <>
      <div>
        {currentItems.length === 0 ? (<h2 className="searchFailed"> 일치하는 항목이 없습니다.</h2>) : currentItems.slice(indexOfFirstItem, indexOfLastItem).map((cock) => (
          <div key={cock.id}>
            <div className="pageList">
              <div className="pageImgBox">
                <img
                  src={`/image/cocktail/${cock.id}.jpg`}
                  alt={cock.name}
                ></img>
              </div>
              <div className="pageInfo">
                <h3 className="pageTitle">이름 : {cock.name}</h3>
                <p>레시피 : {cock.recipe}</p>
                {cock.quality && <p>특징 : {cock.quality}</p>}
                {cock.taste && <p>맛 : {cock.taste}</p>}
                <p>도수 : 약 {cock.vol}도</p>
                <a href={cock.info}>추가정보</a>
             
              </div>
            </div>
          </div>
        ))}
        <Paging
          totalItems={totalItemsCount}
          itemsPerPage={itemsPerPage}
          onPageChange={handlePageChange}
        />
      </div>
    </>
  );
}

export default CockTail;
